@import "./variables.scss";

@mixin title() {
  font-size: 40px;
  text-align: center;
  font-weight: 400;
  text-transform: uppercase;
}

@mixin small_title() {
  display: block;
  text-align: center;
  margin-top: 5px;
  color: rgba($black, 0.6);
}

header#header {
  background: white;
  @media (max-width: 620px) {
    padding: 15px 0;
    .wrapper {
      height: auto !important;
      address {
        flex-direction: column;
        align-items: flex-end !important;
        gap: 5px !important;
        font-size: 14px !important;
      }

      .logo {
        max-height: 40px !important;
      }
    }
  }
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;

    .logo {
      max-height: 50px;
    }

    address {
      font-style: normal;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 30px;
      font-size: 16px;

      .street,
      .phone {
        display: flex;
        align-items: center;

        svg {
          width: 24px;
          height: auto;
          fill: rgba($black, 0.8);
          margin-left: 10px;
        }
      }

      span {
        display: inline-block;
        text-align: right;
        color: rgba($black, 0.8);
      }
    }
  }
}

section#main {
  position: relative;
  @media (max-width: 720px) {
    .info-block {
      padding: 30px 0;
    }
  }

  min-height: 600px;
  max-height: 850px;

  .bg {
    height: calc(100vh - 80px);
    min-height: 600px;
    max-height: 850px;
    width: 100%;
    position: absolute;
    background-size: cover;
    background-position: center center;
    opacity: 0;
    transition: 0.8s;
    z-index: -1;
    bottom: 0;
    // border-radius: 20px;

    &.active {
      opacity: 1;
    }
  }

  .info-container {
    position: relative;
    height: calc(100vh - 80px);
    min-height: 600px;
    max-height: 850px;
    padding-top: 20vh;

    @media (max-width: 768px) {
      display: flex;
      justify-content: center !important;
      align-items: flex-start;
    }

    .info-block {
      max-width: 600px;
      width: 100%;
      left: 0;
      bottom: 50px;
      backdrop-filter: blur(9.5px);
      background: rgba(#fff, 0.55);
      border-radius: 40px;
      display: flex;
      flex-direction: column;
      padding: 30px 30px;
      text-align: center;
      justify-content: center;
      align-items: center;

      h1,
      p {
        color: $black;
        // background: $dark_green;
      }
      @media (max-width: 524px) {
        h1 {
          font-size: 30px !important;
        }
        p {
          font-size: 18px !important;
        }
      }

      h1 {
        font-size: 36px;
        font-weight: 500;
      }

      p {
        font-size: 22px;
        margin: 15px 0 28px 0;
      }
    }
  }
}

button {
  width: 180px;
  height: 50px;
  background: $light_green;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  transition: background 0.4s;
  color: #fff;
  font-size: 16px;
  outline: none;
  &:hover {
    background: $hover_green;
  }
}

select {
  min-width: 100px;
  height: 30px;
  background: none;
  border-radius: 5px;
  border: 1px solid rgba($black, 0.4);
  padding: 0 10px;
  option {
    font-size: 15px;
    font-family: "Open Sans", sans-serif;
  }
}

section#second {
  @media (max-width: 680px) {
    .wrapper {
      padding: 0 50px;
    }
  }

  padding: 100px 0;
  h1 {
    @include title();
  }

  .benefits-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 40px;
    place-items: center;
    margin-top: 50px;

    .benefit-item {
      width: 100%;
      display: grid;
      grid-template-rows: 200px auto auto;
      place-items: center;
      text-align: center;
      box-shadow: 0px 0px 10px 0 rgba($black, 0.2);
      padding: 50px 30px;
      border-radius: 10px;

      .img {
        width: 100%;
        height: 200px;
        background-size: cover;
        background-position: center center;
        border-radius: 5px;
      }

      p {
        font-size: 30px;
        min-height: 60px;
        margin-top: 20px;

        @media (max-width: 720px) {
          font-size: 26px;
        }
      }

      span {
        font-size: 15px;
        color: rgba($black, 0.6);
        margin-top: 20px;
      }

      img {
        width: 150px;
        clip: auto;
      }
    }
  }
}

section#catalog {
  @media (max-width: 1300px) {
    .wrapper {
      padding: 0 50px;
    }
  }
  padding: 50px 0;
  h1 {
    @include title();
  }
  span {
    @include small_title();
  }
  .catalog {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
    place-items: center;
    gap: 40px;
    margin-top: 50px;

    &__item {
      width: 100%;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      display: grid;
      place-items: center;
      grid-template-rows: minmax(215px, auto) minmax(36px, auto) auto;
      padding: 30px 15px 15px 15px;
      position: relative;
      z-index: 2;

      @media (max-width: 680px) {
        max-width: 400px;
      }

      &-img {
        position: relative;

        canvas {
          width: 100% !important;
        }

        .shadow {
          position: absolute;
          width: 130px;
          height: 130px;
          background: #000000;
          opacity: 0.2;
          filter: blur(25px);
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }

      .row {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;

        .price {
          font-weight: 700;
          font-size: 22px;
        }
      }

      button {
        width: 100%;
        margin-top: 20px;
        height: 44px;

        &.disable {
          pointer-events: none;
        }
      }

      .reset {
        max-width: 20px;
        position: absolute;
        padding: 15px;
        box-sizing: content-box;
        top: 0;
        right: 0;
        cursor: pointer;
        opacity: 0.5;
        transition: opacity 0.3s;

        &:hover {
          opacity: 1;
        }
      }

      .color-name {
        position: absolute;
        top: 15px;
        color: rgba($black, 0.4);
      }

      @keyframes drawing {
        0% {
          transform: rotateZ(0deg);
        }

        30% {
          transform: rotateZ(15deg);
        }

        60% {
          transform: rotateZ(-10deg);
        }

        80% {
          transform: rotateZ(8deg);
        }

        100% {
          transform: rotateZ(0deg);
        }
      }

      .change-color,
      .drawing {
        max-width: 30px;
        position: absolute;
        left: 15px;
        top: 15px;
        cursor: pointer;
        transition: opacity 0.3s;

        &:hover {
          opacity: 0.6;
        }
      }

      .drawing {
        top: 55px;
      }

      .razmetka {
        font-size: 12px;
        color: rgba($black, 0.5);
        position: absolute;
        top: 42px;
      }

      img {
        max-width: 200px;
      }

      .name {
        font-size: 18px;
        font-weight: 500;
        text-align: center;
      }

      .bg,
      .color-bg {
        position: absolute;
        width: 100%;
        height: 100%;
        background: #fff;
        top: 0;
        border-radius: 10px;
        z-index: -1;
      }

      .color-bg {
        background: none;
        mix-blend-mode: screen;
        z-index: auto;
        opacity: 1;
      }

      .palette {
        position: absolute;
        left: 0;
        z-index: -1;
        transition: left 0.3s;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        flex-direction: column;
        gap: 3px;

        .color {
          width: 30px;
          height: 30px;
          border-radius: 15px;
          box-shadow: 0 0 4px 0 rgba($black, 0.2);
          cursor: pointer;
          transition: opacity 0.3s;

          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }
}

#partners {
  margin: 100px 0;
  background: $dark_green;
  padding: 50px 0;
  h1 {
    @include title();
    color: #fff;
  }

  .partners-container {
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
    .partners-item {
      width: 200px;
      height: 130px;
      background: #fff;
      border-radius: 10px;
      display: grid;
      place-items: center;
      box-shadow: 0px 0px 10px 0px rgba($black, 0.2);

      img {
        width: 60%;
      }
    }
  }
}

#contacts {
  padding: 50px 0;
  h1 {
    @include title();
  }

  .map-container {
    border-radius: 20px;
    overflow: hidden;
  }

  .contacts-container {
    position: relative;
    margin: 50px 0 20px 0;
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    gap: 30px;
    box-shadow: 0 0 10px 0 rgba($black, 0.2);
    border-radius: 20px;
    z-index: 10;
    background: #fff;

    @media (max-width: 886px) {
      justify-content: center;
    }

    article {
      display: flex;
      align-items: center;
      svg {
        margin-right: 10px;
      }
    }
  }
}

#cart {
  top: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba($black, 0.6);
  z-index: 999;

  @media (max-width: 580px) {
    padding: 0 20px;
    .cart-container {
      width: 100% !important;
    }
  }

  .success {
    min-height: 350px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    svg {
      width: 100px;
      height: auto;
      fill: $light_green;
    }

    p {
      text-align: center;
      font-size: 20px;
    }
  }

  .cart-container {
    position: relative;
    width: 550px;
    background: #fff;
    border-radius: 20px;
    padding: 30px;
    margin: 100px auto;

    h1 {
      font-size: 20px;
      font-weight: 500;
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        margin-right: 5px;
      }
    }

    form {
      display: flex;
      flex-wrap: wrap;
      gap: 15px;
      justify-content: center;
      @media (max-width: 580px) {
        input {
          width: 100% !important;
        }
        button {
          width: 100% !important;
        }
      }
      input {
        width: 48%;
        height: 40px;
        border: 1px solid rgba($black, 0.2);
        padding: 0 15px;
        box-sizing: border-box;
        outline: none;
        border-radius: 5px;
      }
    }

    .cart-items {
      margin: 40px 0 10px 0;
      overflow-y: scroll;
      max-height: 250px;
      .empty {
        color: rgba($black, 0.5);
        text-align: center;
      }
      .cart-item {
        display: flex;
        align-items: center;
        min-height: 50px;
        column-gap: 20px;
        row-gap: 10px;
        border-bottom: 1px solid rgba($black, 0.2);

        @media (max-width: 580px) {
          flex-wrap: wrap;
          padding: 8px 0;

          .name {
            width: 100% !important;
            max-width: none !important;
          }

          .price {
            margin-left: 0 !important;
          }
        }

        .name {
          min-width: 200px;
          max-width: 200px;
          font-size: 15px;
        }

        .price {
          font-weight: 700;
          white-space: nowrap;
        }

        .count {
          width: 50px;
          border: none;
          border-bottom: 1px solid rgba($black, 0.2);
          padding: 0 5px;
          outline: none;
        }
      }
    }

    .close {
      position: absolute;
      width: 30px;
      height: auto;
      right: 15px;
      top: 15px;
      cursor: pointer;
      transition: opacity 0.3s;

      &:hover {
        opacity: 0.5;
      }
    }
  }
}

.cart-button {
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 500;
  width: 50px;
  height: 50px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  -webkit-align-items: center;
  justify-content: center;
  align-items: center;
  background: $second_green;
  box-shadow: 0px 0px 5px 0 rgba(#fff, 0.5);

  .count {
    position: absolute;
    right: 10px;
    top: 10px;
    display: grid;
    place-items: center;
    height: 14px;
    width: 14px;
    font-size: 12px;
    background: #fff;
    border-radius: 50%;
  }

  svg {
    width: 25px;
    height: auto;
    fill: #fff;
    transform: translate(-2px, 2px);
  }

  &::after,
  &::before {
    content: "";
    position: absolute;
    border: 2px solid $light_green;
    left: -10px;
    opacity: 0;
    right: -10px;
    top: -10px;
    bottom: -10px;
    border-radius: 50%;
    animation: pulse 2.5s linear infinite;
  }

  &::after {
    animation-delay: 1.25s;
  }

  @keyframes pulse {
    0% {
      transform: scale(0.5);
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: scale(1.2);
      opacity: 0;
    }
  }
}
